<template>
  <section class="banner_page position-relative" v-if="slide.photo">
    <div
      class="top_img parallax"
      data-depth="0.40"
      :style="`background-image: url('./img/${slide.photo}')`"
      ref="parallax"
    ></div>
    <div class="container h-100 position-relative z-index-1">
      <div class="row h-100 mx-0 align-items-center">
        <div class="overflow-hidden px-lg-5 px-0" v-if="slide">
          <h2 class="page-title box">【 {{slide.name}} 】</h2>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data () {
    return {
      slide: {}
    }
  },
  methods: {
    simpleScrollTrigger () {
      const depth = this.$refs.parallax.dataset.depth
      const movement = -(this.$refs.parallax.offsetHeight * depth)
      this.gsap.to('.parallax', {
        y: movement,
        ease: 'none',
        scrollTrigger: {
          trigger: 'body',
          start: 'top',
          scrub: 1
        }
      })
      this.gsap.from('.box', {
        y: 50,
        opacity: 0,
        duration: 1.5,
        scrollTrigger: {
          trigger: '.box',
          start: 'top 90%'
        }
      })
    }
  },
  mounted () {
    const vm = this
    const url = './json/pagesBanner.json'
    this.$http.get(url)
      .then((response) => {
        response.data.forEach(function (item) {
          if (item.type === vm.$route.name) {
            vm.slide = item
          }
        })
      })
      .then(() => {
        this.simpleScrollTrigger()
      })
  }
}
</script>
