<template>
  <loading :active="isLoading"></loading>
  <pagesBanner></pagesBanner>
  <section class="news pages position-relative overflow-hidden">
    <div class="container position-relative z-index-1">
      <div class="bread-crumbs py-3 px-lg-5">
        <ol
          itemscope=""
          itemtype="http://schema.org/breadcrumbList"
          id="breadcrumbs"
        >
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <router-link to="/" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
            <meta itemprop="position" content="1" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link to="/news" itemprop="item" title="最新消息">
              <span itemprop="name">最新消息</span>
            </router-link>
            <meta itemprop="position" content="2" />
          </li>
        </ol>
      </div>
      <div class="main-box bg-white px-lg-5 px-4 pt-lg-5 pt-4 pb-3 mb-4">
        <div class="text-center mb-lg-5 mb-3">
          <h2 class="c_h2">最新消息</h2>
        </div>
        <div class="row news-list mx-n2" ref="newsItem">
          <div class="col-lg-4 col-sm-6 mb-lg-5 mb-4 px-2 item" v-for="(item, index) in companiesLoaded" :key="index">
            <router-link :to="`/news/${item.id}`">
              <div class="inner bg-white p-4">
                <div class="img mb-3">
                  <img :src="item.image" :alt="item.title" class="w-100" />
                </div>
                <div class="title mb-2">
                  {{item.title}}
                </div>
                <div class="info mb-2">
                  {{item.description}}...
                  <span>閱讀更多</span>
                </div>
                <div class="date">
                  <img src="@/assets/img/clock.svg" alt="" class="me-2" />{{ $filters.date(item.create_at)}}
                </div>
              </div>
            </router-link>
          </div>
        </div>
        <div id="gallery-btn" class="text-center col-12 mb-5" v-if="commentsToShow < articles.length || articles.length > commentsToShow">
          <div class="scroll_line mb-3"></div>
          <span class="mouse"></span>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import pagesBanner from '../components/pagesBanner.vue'
export default {
  components: {
    pagesBanner
  },
  data () {
    return {
      articles: [],
      isLoading: false,
      windowWidth: window.innerWidth,
      commentsToShow: 3,
      showNum: 3,
      totalComments: 0,
      bottom: false
    }
  },
  created () {
    window.addEventListener('scroll', () => {
      this.bottom = this.bottomVisible()
    })
    this.getData()
  },
  watch: {
    windowWidth () {
      if (this.windowWidth <= 991) {
        this.showNum = 4
      } else {
        this.showNum = 3
      }
    },
    bottom (newValue) {
      if (newValue & this.commentsToShow < this.totalComments) {
        this.showMore()
      }
    }
  },
  methods: {
    getData (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/articles?page=${page}`
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false
          if (res.data.success) {
            this.articles = res.data.articles
            this.totalComments = res.data.articles.length
          }
        })
        .then(() => {
          this.gsap.from('.item', {
            scrollTrigger: '.news-list',
            duration: 1.15,
            y: 50,
            opacity: 0,
            stagger: 0.3
          })
        })
    },
    onResize () {
      this.windowWidth = window.innerWidth
    },
    showMore () {
      if (this.commentsToShow > this.articles.length) return
      this.commentsToShow = this.commentsToShow + this.showNum
    },
    bottomVisible () {
      const scrollY = window.scrollY
      const visible = document.documentElement.clientHeight
      const pageHeight = document.documentElement.scrollHeight
      const bottomOfPage = visible + scrollY >= pageHeight
      return bottomOfPage || pageHeight < visible
    }
  },
  computed: {
    companiesLoaded () {
      return this.articles.slice(0, this.commentsToShow)
    }
  },
  mounted () {
    if (this.windowWidth <= 991) {
      this.showNum = 4
      this.commentsToShow = 4
    } else {
      this.showNum = 3
      this.commentsToShow = 3
    }
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize)
    })
    this.totalComments = this.articles.length
  },
  beforeUnmount () {
    window.removeEventListener('resize', this.onResize)
  }
}
</script>
